.btn {

  --bs-btn-padding-x: 2rem;
  --bs-btn-padding-y: .7rem;
  font-size: 12px;
  font-weight: 500;
  padding: 5px 20px;
  user-select: none;
  text-decoration: none !important;
  width: fit-content;

  &-big {
    font-size: 14px;
  }

  &-outline{

    &-primary {
      // --bs-btn-border-color: #{$COLOR-ORANGE};
      // --bs-btn-color: #{$COLOR-ORANGE};
      // --bs-btn-hover-color: #fff;
      // --bs-btn-hover-bg: #dd480a;
      // --bs-btn-focus-shadow-rgb: 49,132,253;
      // --bs-btn-active-color: #fff;
      // --bs-btn-active-bg: #{$COLOR-ORANGE};
      // --bs-btn-active-border-color: #{$COLOR-ORANGE};
      // --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
      // --bs-btn-disabled-color: #fff;
      // --bs-btn-hover-border-color: #{$COLOR-ORANGE};
      // --bs-btn-disabled-bg: #{$COLOR-ORANGE};
      // --bs-btn-disabled-border-color: #{$COLOR-ORANGE};
    }

  }

  &-secondary {
    --bs-btn-color: #000;
    --bs-btn-bg: #{$COLOR-SECONDARY};
    --bs-btn-border-color: #{$COLOR-SECONDARY};
    --bs-btn-hover-color: #000;
    --bs-btn-hover-bg: #f1a549;
    --bs-btn-hover-border-color: #{$COLOR-SECONDARY};
    --bs-btn-focus-shadow-rgb: 49,132,253;
    --bs-btn-active-color: #{$COLOR-SECONDARY};
    --bs-btn-active-bg: #{$COLOR-SECONDARY};
    --bs-btn-active-border-color: #{$COLOR-SECONDARY};
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: #{$COLOR-SECONDARY};
    --bs-btn-disabled-border-color: #{$COLOR-SECONDARY};
  }

  &-icon {
    position: relative;
    transition: all ease-in-out .2s;
    text-decoration: none;
    user-select: none;
    display: flex;
    align-items: center;
    width: fit-content;
    
    & img {
      margin-right: 8px;
    }
    
    &:link:hover {
      font-weight: 700;
      text-decoration: none;
    }
  }

}

